import { Node, Schema } from '@tiptap/pm/model'
import { getTextSerializersFromSchema, Range, TextSerializer } from '@tiptap/core'

type TextMapperOptions = {
    schema?: Schema
    blockSeparator?: string
    textSerializers?: Record<string, TextSerializer>
}

export class TextMapper {
    private mapping?: number[]
    private blockSeparator: string
    private textSerializers?: Record<string, TextSerializer>

    constructor(document: Node, _text: string, options?: TextMapperOptions) {

        const { schema, blockSeparator, textSerializers } = options || {}

        this.blockSeparator = blockSeparator || "\n\n"
        this.textSerializers = {
            ...(textSerializers || {}),
            ...(schema ? getTextSerializersFromSchema(schema) : {}),
        }

        const result = this.mapTextToModel(document, { isSeparated: true, modelIndex: -1, textIndex: 0, mappings: new Map<number, number>() })

        this.mapping = []
        const keys = [...result.mappings.keys()]
        keys.forEach(k => {
            this.mapping![k] = result.mappings.get(k)!
        })
    }

    getMapping(): number[] {
        return Array.from(this.mapping!)
    }

    getRange(textOffset: number, length: number): Range|undefined {
        const mapping = this.getMapping()

        if (mapping[textOffset] && (textOffset + length) <= mapping.length) {
            return {
                from: mapping[textOffset],
                to: mapping[textOffset + length - 1] + 1
            }
        }
        console.warn(`Failed to get Range for textOffset: ${textOffset} and lenght ${length}`)
        return
    }

    public getTextOffset(modelOffset: number): number {
        const textOffset = this.mapping!.findIndex(e => e >= modelOffset)
        return textOffset
    }

    private mapTextToModel(node: Node, result: Result): Result {
        const textSerializer = this.textSerializers?.[node.type.name]

        if (textSerializer) {
            if (node.isBlock && !result.isSeparated) {
                result.textIndex += this.blockSeparator!.length
                result.isSeparated = true
            }

            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            result.textIndex += textSerializer({ node, pos: result.modelIndex, parent: undefined, index: undefined, range: undefined }).length
            result.modelIndex += node.nodeSize
        } else if (node.isText) {
            result.isSeparated = false
            const chars = [...node.text!]

            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            chars.forEach(_c => {
                result.mappings.set(result.textIndex, result.modelIndex)
                result.textIndex++
                result.modelIndex++
            })
        } else if (node.isBlock) {
            if (!result.isSeparated) {
                result.textIndex += this.blockSeparator.length
                result.isSeparated = true
            }

            result.modelIndex++


            for (let i = 0; i < node.content.childCount; i++) {
                result = this.mapTextToModel(node.content.child(i), result)
            }
            result.modelIndex++
        } else {
            result.modelIndex += node.nodeSize
        }
        return result
    }
}

type Result = {
    modelIndex: number
    textIndex: number
    mappings: Map<number, number>
    isSeparated: boolean
}

